.transaction-history {
    width: 90%;
    margin: 20px auto 0;
}

.transaction {
    font-size: 15px;
    font-family: "Lato";
    color: #5c6975;
    text-align: center;
}

table {
    border-collapse: collapse;
    box-shadow: 0px 1px 3px #bfcddebf;

}


thead tr {
    background: #00bcd4;
    padding: 0;
    height: 50px;
    font-size: 13px;
    font-family: "Lato";
    color: #ffffff;
    text-transform: uppercase;
}

tbody tr {
    height: 52px;
}

tbody tr:nth-child(2n) {
    background-color: #ecf2f3;
}